@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  li {
    @apply p-4 hover:text-rose-600
  }

  button {
    @apply text-white py-2 px-4 bg-rose-500 font-semibold rounded-lg shadow-md hover:shadow-xl hover:bg-rose-700 transition duration-300
  }
}

@font-face {
  font-family: "BarlowLight";
  src: local("BarlowLight"),
    url("./fonts/Barlow-Light.ttf") format("truetype");
  font-weight: light;
}

@font-face {
  font-family: "BarlowThin";
  src: local("BarlowThin"),
    url("./fonts/Barlow-Thin.ttf") format("truetype");
  font-weight: thin;
}

@font-face {
  font-family: "BarlowBold";
  src: local("BarlowBold"),
    url("./fonts/Barlow-Bold.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "BarlowRegular";
  src: local("BarlowRegular"),
    url("./fonts/Barlow-Regular.ttf") format("truetype");
}

.componentClass {
  width: 8%;
  margin: auto;
}

@media (max-width: 1216px) {
  .componentClass {
    width: 15%;
    margin: auto;
  }
}

@media (max-width: 736px) {
  .componentClass {
    width: 25%;
    margin: auto;
  }
}

@media (max-width: 429px) {
  .componentClass {
    width: 35%;
    margin: auto;
  }
}